.banner {
  background: #232844;
  background-repeat: repeat;
}

.banner .wrapper img#logo {
  padding: 3em 0;
}

.banner h1#title {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

.navbar-wrapper {
  background: #3b5998;
}

.navbar-wrapper p {
  margin: 0;
}

nav.nav {
  background: #1f1a33;
}

nav.nav a, nav.nav label {
  text-transform: uppercase;
  font-size: 1.25em;
  text-decoration: none;
  font-weight: 700;
  color: #ffffff;
  margin-right: 2em;
}

nav.nav a:hover, nav.nav label:hover {
  color: #ff4a15;
}

nav.nav label {
  margin: 0;
  border-right: 1px solid #ff4a15;
  padding-right: 1.5em;
}

nav.nav label:hover {
  color: #ffffff;
}

nav.nav input[type='search'] {
  background: transparent;
  outline: none;
  border: none;
  color: #ffffff;
  font-size: 1.25em;
  font-weight: 400;
  font-style: italic;
  padding: 2em 1em;
}

@media (max-width: 900px) {
  .banner h1 {
    padding: 1.5em 0;
    font-size: 3.5em;
  }
  nav.nav a, nav.nav label {
    font-size: 1.1em;
    margin-right: 1em;
  }
  nav.nav label {
    font-size: 0.9em;
    font-weight: 500;
    text-transform: capitalize;
  }
  nav.nav input[type='search'] {
    font-size: 1.1em;
    padding: 1.5em .5em;
  }
}

@media (max-width: 600px) {
  .banner h1 {
    font-size: 2.5em;
  }
  nav.nav .wrapper {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    max-width: 100%;
  }
  nav.nav .wrapper a {
    flex-basis: 50%;
    padding: 1em 0;
    text-align: center;
    margin: 0;
  }
  nav.nav .wrapper label {
    display: none;
  }
  nav.nav .wrapper input[type='search'] {
    flex-basis: 75%;
    text-align: center;
    background: rgba(128, 128, 128, 0.2);
    border-radius: 100px;
    padding: .5em 0;
    margin: .5em 3em;
    background-image: url(../images/search.svg);
    background-repeat: no-repeat;
    background-position: 20px 10px;
    background-size: 20px;
  }
  nav.nav .wrapper a, nav.nav .wrapper label, nav.nav .wrapper input[type='search'] {
    font-size: 1.1em;
  }
}

.resources {
  padding: 0;
  margin: 0;
  list-style: none;
}

.resources li {
  width: 25%;
  float: left;
  display: inline-block;
  position: relative;
  overflow: hidden;
  transition: transform 0.4s, opacity 0.1s 0.3s;
  margin-top: -5px;
}

.resources li img {
  width: 100%;
  max-width: 100%;
}

.resources li .resource-info {
  background: rgba(255, 74, 21, 0.85);
  width: 100%;
  height: 100%;
  padding: 2em 10%;
  position: absolute;
  top: auto;
  bottom: 0;
  opacity: 0;
  transform: translateY(100%);
  transition: transform 0.4s, opacity 0.1s 0.3s;
}

.resources li .resource-info h2 {
  font-size: 1.5em;
  line-height: 3em;
  text-transform: uppercase;
  color: #ffffff;
}

.resources li .resource-info .button {
  padding: 1em 1.5em;
  border: 2px solid #ffffff;
  border-radius: .5em;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: .9em;
  font-weight: 700;
  transition: all .4s ease-in-out;
}

.resources li .resource-info .button:hover {
  border: 2px solid #232844;
  color: #232844;
}

.resources li .resource-info .price {
  display: inline;
  font-weight: 700;
  padding-left: 1.5em;
  color: #232844;
}

.resources li:hover img {
  transform: scale(1.5);
  transition: transform 0.4s, opacity 0.1s;
}

.resources li:hover .resource-info {
  opacity: 1;
  transform: translateY(0px);
  transition: transform 0.4s, opacity 0.1s;
}

@media (max-width: 2000px) {
  img#logo {
    width: 70%;
  }
}

@media (max-width: 1250px) {
  img#logo {
    width: 60%;
  }
  .resources li {
    width: 33.33%;
  }
}

@media (max-width: 900px) {
  .resources li {
    width: 50%;
  }
}

@media (max-width: 600px) {
  .banner .wrapper img#logo {
    padding: 2em 0;
  }
  nav.nav .wrapper a, nav.nav .wrapper input[type='search'] {
    font-size: .7em;
  }
  nav.nav .wrapper input[type='search'] {
    background-position: 20px 5px;
    background-size: 15px;
    margin-bottom: 1.5em;
  }
  .resources li {
    width: 100%;
  }
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.wrapper {
  width: 1140px;
  max-width: 90%;
  margin: 0 auto;
}
